<template>

  <div class="wrap">
    <div>
      <video id="gum-local" autoplay playsinline></video>
    </div>
    <iframe ref="iframe" :src="htmlSrc" width="500px" height="500px" frameborder="0">
    </iframe>
    <button @click="vueSendMsg">vue向iframe传递信息</button>
    <button @click="iframeMethods">触发iframe中的方法</button>
  </div>

</template>

<script>
// import aa from './sssF.vue';
export default {
  data() {
    return {
      as: null,
      htmlSrc: '/aa.html',
      facilityId: ''
    }
  },
  components: {
    // aa
  },
  created() {

  },
  methods: {
    async getVideoConfig() {
      let cmList = await navigator.mediaDevices.enumerateDevices();
      cmList = cmList.reverse(); // 翻转数组, 可能主摄都是从下往上的
      console.log(cmList, '全部数据列表');
      if (cmList.length == 0) return;
      // 拿到video列表 找后置 这里的固定了black 可能有些设备没有back这个标识的, constraints设置后置就行
      let assignVideo = cmList.filter(x => x.kind == 'videoinput' && x.label.includes('back'));
      console.log(assignVideo, '所有后置摄像头列表');
      // 做兼容, 如果有0开头的摄像头就用这个 
      for (let index = 0; index < assignVideo.length; index++) {
        if (assignVideo.label.includes('0')) this.facilityId = assignVideo[index]?.deviceId || '';
      }
      // 如果没有就用反转后默认的第一个
      if (!this.facilityId) this.facilityId = assignVideo[0]?.deviceId || '';

      console.log(this.facilityId, '当前设备id');
      const constraints = {
        audio: false,
        video: { deviceId: this.facilityId ? { exact: this.facilityId } : undefined }
      };
      const stream = await navigator.mediaDevices.getUserMedia(constraints);
      const video = document.querySelector('video');
      video.srcObject = stream;

    },
    // vue获取iframe传递过来的信息
    getiframeMsg(event) {
      const res = event.data;
      console.log(event)
      if (res.cmd == 'myIframe') {
        console.log(res, 'iframe给组件传递')
        this.vueSendMsg();
      }
    },
    // vue向iframe传递信息
    vueSendMsg() {
      // const onloadRef = this.$refs.iframe;
      const iframeWindow = this.$refs.iframe.contentWindow;
      iframeWindow.postMessage({
        cmd: 'myVue',
        params: {
          info: 'Vue向iframe传递的消息',
        }
      }, '*')
    },
    // 触发iframe中的方法
    iframeMethods() {
      this.$refs.iframe.contentWindow.triggerByVue('通过Vue触发iframe中的方法');
    },
  },
  mounted() {
    // getVideo
    this.getVideoConfig();
    window.addEventListener('message', this.getiframeMsg)
    this.vueSendMsg()
  }
}

</script>
<style scoped>
div {
  width: 1.5rem;
}
</style>